.header {
  font-size: 2vw;
  width: 100vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  color: white;
  background: #222;
}

.header h1 {
  margin: 20px auto;
}

.header h1:nth-of-type(1) {
  margin-right: 1%;
}

.header h1:nth-of-type(2) {
  margin-left: 1%;
}



